<template>
    <el-card class="box-card">
        <div>
            <h2>Cuby</h2>
            <el-input v-model="name" placeholder="请输入昵称">
                <i slot="prefix" class="el-input__icon el-icon-edit"></i>
            </el-input>
            <el-input v-model="tel" placeholder="请输入手机号或者邮箱">
                <i slot="prefix" class="el-input__icon el-icon-message"></i>
            </el-input>
            <el-input placeholder="请输入密码" v-model="password" show-password>
                <i slot="prefix" class="el-input__icon iconfont icon-lock-fill"></i>
            </el-input>
            <el-button type="primary">注册</el-button>
        </div>
        <a>已有账号？立即登录</a>
    </el-card>
</template>

<script>
export default {
    data() {
            return {
                name: '',
                tel: '',
                password: ''
            }
        }
    }
</script>

<style scoped>
    h2{
        text-align: center;
        letter-spacing: 2px;
        margin:0;
    }

    .el-card{
        margin:0 auto 0;
        position: relative;
        padding-bottom:20px;
    }

    .el-card>div>div{
        width:250px;
        margin:0 auto;
        line-height: 56px;
    }

    .el-card a{
        display: block;
        width:auto;
        font-size: 13px;
        position: absolute;
        bottom:6px;
        right:10px;
        cursor: pointer;
    }

    .el-button{
        width:100%;
        height:35px;
        letter-spacing: 2em;
        text-indent: 2em;
        padding:0;
    }

    .box-card {
        width: 360px;
    }

    .iconfont{
        margin-left:5px;
    }
</style>


