        <div class="art_mid">
            这是一段文章的内容这是一段文章的内容这是一段文章的内容这是一段文章的内容这是一段文章的内容这是一段文章的内容这是一段文章的内容这是一段文章的内容这是一段文章的内容这是一段文章的内容这是一段文章的内容这是一段文章的内容这是一段文章的内容这是一段文章的内容这是一段文章的内容这是一段文章的内容这是一段文章的内容
            这是一段文章的内容
        </div>
        <div class="art_foot">
            <div>点赞</div>
            <div>收藏</div>
            <div id="report">举报</div>
            <div class="clear_both"></div>
        </div>
    </div>
</template>

<script>
export default {
    name:'full-art',
    props: {
        art_id:{
            type:Number,
            default: 0
        },
    },
    data () {
        return {
            label_list: ['标签','标签','标签'],
            art_detail: [{k:'这是键', v:'这是值'}],
            title:'标题'
        }
    }
}
</script>

<style scoped>
@import "../assets/tag.css";

.full_article_block{
    padding:0;
}

h1{
    margin:0 0 15px 0;
    font-size: 30px;
}

ul{
    padding-left: 15px;
    font-size: 13px;
}

ul li{
    list-style: none;
    float: left;
    margin-right:50px;
}

.tag_div{
    margin: 20px 0 0 0;
    padding-left: 10px;
}

.art_mid{
    font-size: 15px;;
    padding:0 1.5em;
    line-height: 27px;
}

.art_foot{
    margin-top:50px;
    padding:0 1.5em;
    font-size: 15px;
    background-color: #eee;
    line-height: 39px;
}

.art_foot>div{
    float: left;
    margin-right:75px;
}

#report{
    float: right;
    margin-right:0;
}
</style>



