<template>
    <div class="info_block">
        <h1>
            <el-tag class="label top" type="info" v-show="top">置顶</el-tag>
            <el-tag class="label" type="info">{{label}}</el-tag>
            <a>{{title}}</a>
        </h1>
        <div class="intro">{{intro}}</div>
        <div class="ft">
            <div class="op">
                <ul>
                    <li>点赞 {{like}}</li>
                    <li>评论 {{comment}}</li>
                    <li>收藏 {{collect}}</li>
                    <li>阅读 {{view}}</li>
                </ul>
            </div>
            <div class="author" v-show="type=='view'">
                {{author_name}}
            </div>
            <div class="edit" v-show="type=='edit'">
                <el-popconfirm
                    title="你确定要删除吗？">
                    <el-button slot="reference">删除</el-button>
                </el-popconfirm>
                <el-button>编辑</el-button>
                <el-popconfirm
                    title="你确定要置顶吗？">
                    <el-button slot="reference">置顶</el-button>
                </el-popconfirm>
            </div>
        </div>
        <div style="clear:both; height:5px"></div>
    </div>
</template>

<script>
export default {
    name: 'arb',
    props: {
        title: {
            type: String,
            default: "标题"
        },
        label: {
            type: String,
            default: "标签"
        },
        url: {
            type: String,
            default: "url"
        },
        intro: {
            type: String,
            default: "这是一段非常长的介绍"
        },
        like: {
            type: Number,
            default: 0
        },
        collect: {
            type: Number,
            default: 0
        },
        comment: {
            type: Number,
            default: 0
        },
        view: {
            type: Number,
            default: 0
        },
        author_src: {
            type: String,
            default: '/'
        },
        author_name: {
            type: String,
            default: '作者'
        },
        type: {
            type:String,
            default: 'view'
        },
        top: {
            type:Boolean,
            default:false
        }
    },
    data () {
        return {
        }
    }
}
</script>

<style scoped>
.info_block{
    border:1px solid #000;
    padding:0 15px;
    width:690px;
    margin-top:15px;
}

.info_block>h1{
    margin:10px 0;
    height:40px;
    line-height: 40px;
    font-size:30px;
    cursor: pointer;
}

.info_block>.intro{
    padding-left: 2em;
    margin:20px 0;
    font-size:15px;
}

.op{
    margin-top:15px;
    font-size:15px;
}

.op ul{
    padding-left:15px;
}

.op li{
    list-style: none;
    float:left;
    margin-right:25px;
    font-size: 13px;
}

.author, .edit{
    float: right;
    font-size:15px;
}

.info_block>h1>.label{
    font-size:13px;
    height:23px;
    margin:0 6px 5px 5px;
    font-weight: normal;
    cursor: default;
    vertical-align: middle;
    line-height: 23px;
}

.info_block>h1>.top{
    margin-right:-5px;
}

.edit .el-button{
    border:0;
    padding:0;
    line-height:20px;
    height:20px;
    margin-left:15px;
}

.edit .el-button:hover, .edit .el-button:focus{
    color:#333;
    background-color: rgba(0, 0, 0, 0);
}

.el-popover{
    background: #fafafa;
}
</style>




