<template>
    <div class="side_block author_info_block">
        <div>
            <el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"></el-avatar>
            <h3>{{name}}</h3>
        </div>
        <div class="author_intro">{{intro}}</div>
        <div style="text-align:center;margin-top:25px"><el-button type="primary" class="subscribe_button" :class="button_title=='关注'?'ls1':''">{{button_title}}</el-button></div>
    </div>
</template>

<script>
export default {
    name: 'aib',
    props: {
        uid:{
            type:String,
            default:undefined
        }
    },
    data () {
        return {
            button_title:'关注',
            circleUrl: "https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png",
            name:'昵称',
            intro:'作者的简介'
        }
    }
}
</script>

<style scoped>
@import "../assets/side_block.css";

.author_info_block>div{
    margin:5px 0 0;
}

.author_info_block>div>h3{
    float: right;
    width:75%;
    margin:0;
    line-height:45px;
    font-size: 21px;
    cursor: pointer;
}

.author_intro{
    font-size: 13px;
    padding:10px 15px;
}

.subscribe_button{
    width:250px;
    text-align: center;
    padding:8px 0;
}

.el-avatar{
    display: inline-block;
    width: 45px;
    height: 45px;
    border: 1px solid #000;
}

.el-avatar>img{
    display:block;
    height:100%;
}

.el-divider{
    margin:0;
}

.ls1{
    letter-spacing: 1em;
    text-indent: 1em;
}
</style>




