<template>
    <div class="side_block list_block">
        <div class="block_edit_icon" v-show="editable"><a>{{edittitle}}</a></div>
        <h3>{{title}}</h3>
        <el-divider></el-divider>
        <el-table
            :data="tableData"
            :show-header="false"
            style="width: 100%; cursor:pointer">
            <el-table-column
                prop="title"
                width="250">
                <template slot-scope="scope">
                    <span style="margin-left: 10px">{{ scope.row.title }}</span>
                    <i class="iconfont" :class="scope.row.visible?'' : 'icon-eye-close'" v-show="type=='collection'"></i>
                    <span v-show="type=='special'"> ({{scope.row.count}})</span>
                </template>
            </el-table-column>
        </el-table>
        <div style="clear:both;float:none"></div>
    </div>
</template>

<script>
export default {
    name: 'crb',
    props: {
        title:{
            type:String,
            default:'标题'
        },
        tableData:{
            type:Array,
            default(){
                return [{title:'标题', count:0, visible:true},{title:'标题', count:1}];
            }
        },
        type:{
            type:String,
            default:'list'
        },
        editable:{
            type:Boolean,
            default:false
        },
        edittitle:{
            type:String,
            default:'编辑'
        }
    },
    data () {
        return {

        }
    }
}
</script>

<style scoped>
@import "../assets/side_block.css";

.list_block_block{
    
}

.list_block>div{
    width:100%;
    text-align: center;
    float: left;
    cursor: pointer;
}

.list_block .data_num{
    font-size: 35px;
    font-weight: bold;
}

.list_block>>>.el-table td, .list_block>>>.el-table th{
    padding:5px 0;
    border: 0;
}

.list_block>>>.el-table::before{
    display: none;
}

.el-divider{
    margin:3px 0 10px;
}

.iconfont{
    font-size: 110%;
}
</style>




