<template>
<div>
  <el-container>
    <el-header>
      <navbar></navbar>
    </el-header>
    <el-container class="mid">
      <el-container>
        <el-main>
            <full-article></full-article>
            <div style="height:15px"></div>
            <comment></comment>
        </el-main>
      </el-container>
      <el-aside width="300px">
        <aib></aib>
        <lb title="作者专栏" type="special"></lb>
        <lb title="相关文章"></lb>
      </el-aside>
    </el-container>
    <el-footer>Footer</el-footer>
  </el-container>
</div>
</template>

<script>
export default {
  name: 'article',
  data () {
    return {
    }
  }
}
</script>


<style>
@import url("//unpkg.com/element-ui@2.13.1/lib/theme-chalk/index.css");
@import url("../assets/common.css");
  
  .el-aside {
    color: #333;
    padding-top:50px;
    padding-bottom:30px;
  }
  
  .el-main {
    color: #333;
    padding-top:50px;
  }
  
  .mid {
    margin-bottom: 40px;
    width:1100px;
    margin:0 auto;
  }
  
  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }
  
  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }

</style>


