<template>
<div>
  <el-container>
    <el-header>
      <navbar></navbar>
    </el-header>
    <div class="title">
        <figure></figure>
        <el-row class="block-col-2 more_option">
            <el-col :span="12">
                <el-dropdown trigger="click">
                <span class="el-dropdown-link">
                    <i class="el-icon-more"></i>
                </span>
                <el-dropdown-menu style="padding:3px 0" slot="dropdown">
                    <el-dropdown-item>举报</el-dropdown-item>
                </el-dropdown-menu>
                </el-dropdown>
            </el-col>
        </el-row>
        <div class="title_mid">
            <div class="portrait"><el-avatar icon="el-icon-user-solid"></el-avatar></div>
            <div class="title_info">
                <h2>{{name}}</h2>
                <div>{{intro}}</div>
            </div>
        </div>
    </div>
    <el-container class="mid">
        <el-container>
            <el-main>
                <arb type="edit"></arb>
                <arb type="edit"></arb>
                <arb type="edit"></arb>
            </el-main>
        </el-container>
        <el-aside width="300px">
            <dab type="only_fans"></dab>
            <udb></udb>
            <lb title="我的收藏" type="collection" edittitle="管理"></lb>
            <lb title="我的专栏" type="special" edittitle="管理"></lb>
        </el-aside>
    </el-container>
    <el-footer>Footer</el-footer>
  </el-container>
</div>
</template>

<script>

export default {
  name: 'user_info',
  data () {
    return {
        name:'昵称',
        intro:'用户的介绍'
    }
  }
}
</script>


<style>
@import url("//unpkg.com/element-ui@2.13.1/lib/theme-chalk/index.css");
@import url("../assets/common.css");
  
  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }
  
  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }

  .title{
      position: absolute;
      background-color: #eee;
      width:100%;
      left:0;
      height:300px;
      top:62px;
  }

  .title_mid{
      margin-bottom: 40px;
      width:1100px;
      margin:0 auto;
      position: absolute;
      bottom:0;
      height:150px;
      left:calc(50% - 550px);
  }

  .title_mid>div{
      line-height: 150px;
      height:150px;
      float:left;
  }

  .portrait>span{
      vertical-align: middle;
      width:80px;
      height:80px;
      line-height: 80px;
  }

  .title_info{
      width:calc(100% - 110px);
      margin-left:30px;
  }

  .title_info>h2{
        height: 50px;
        line-height: 50px;
        margin: 34px 0 5px;
  }

  .title_info>div{
      line-height: 20px;
      font-size: 15px;
  }

  .mid{
      margin-top:362px !important;
  }

  .more_option{
      position: absolute;
      bottom:10px;
      right:20px;
      width:auto;
      cursor: pointer;
  }
</style>


