<template>
    <div class="ccb">
        <div>
            <div style="height:40px">
                <el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"></el-avatar>
            </div>
            <h5 class="name">张三</h5>
            <span class="time">6小时前</span>
        </div>
        <div class="clear_both"></div>
        <div style="padding:0 3em">
            草 ...... 是一种植物
        </div>
        <div class="clear_both"></div>
        <div style="float:none">
            <el-button>点赞 {{like}}</el-button>
            <el-button class="hover_ccb_show">举报</el-button>
            <el-button class="hover_ccb_show">回复</el-button>
        </div>
        <div class="clear_both"></div>
    </div>
</template>

<script>
export default {
    name:'comment',
    props: {
        comment_id:{
            type:Number,
            default: 0
        },
    },
    data () {
        return {
            like:10
        }
    }
}
</script>

<style scoped> 
    .ccb{
        padding: 15px 0 5px;
    }

    h5, .ccb div{
        float: left;
        line-height:40px;
        margin:0;
        font-size: 15px;
    }

    h5{
        margin-left:1em;
        cursor: pointer;
    }

    .time{
        margin-left:1em;
        color:#aaa;
    }

    .el-button{
        border:0;
        background-color: rgb(0, 0, 0, 0);
        padding:5px;
        line-height: 18px;
        float: right;
        margin: 0 10px;
    }

    .hover_ccb_show{
        display: none;
    }

    .ccb:hover .hover_ccb_show{
        display: inline-block;
    }
</style>



