<template>
    <div class="side_block user_detail_block">
        <div class="block_edit_icon" v-show="editable"><a>编辑</a></div>
        <h3>个人信息</h3>
        <el-divider></el-divider>
        <ul>
            <li v-for='(list,i) in info'>{{list.k}}：{{list.v}}</li>
        </ul>
        
        <div style="clear:both;float:none"></div>
    </div>
</template>

<script>
export default {
    name: 'udb',
    props: {
        info:{
            type:Array,
            default:function(){
                return [{k:'生日',v:'1998年11月5日'},{k:'生日',v:'1998年11月5日'}];
            }
        },
        editable:{
            type:Boolean,
            default:false
        }
    },
    data () {
        return {
        }
    }
}
</script>

<style scoped>
@import "../assets/side_block.css";

.user_detail_block{
    
}

.user_detail_block ul{
    padding-left:1em;
}

.user_detail_block li{
    list-style: none;
    line-height: 30px;
    font-size: 15px;
}

.el-divider{
    margin:0;
}
</style>




