import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index.vue'
import search from '@/components/search.vue'
import article_block from '@/components/comment_block.vue'
import create from '@/components/create.vue'
import user_info from '@/components/user_info.vue'
import login from '@/components/register_block.vue'
import article from '@/components/article.vue'
import not_found from '@/components/not_found.vue'


Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/cl',
      name: 'article_block',
      component: article_block
    },
    {
      path: '/search',
      name: 'search',
      component: search
    },
    {
      path: '/create',
      name: 'create',
      component: create
    },
    {
      path: '/userInfo/:id',
      name: 'userInfo',
      component: user_info
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/article/:id',
      name: 'article',
      component: article
    },
    {
      path: '*',
      name: 'not_found',
      component: not_found
    }
  ],
})



