<template>
    <div class="side_block label_block">
        <h3>相关标签</h3>
        <el-divider></el-divider>
        <div v-for='title in label_list'><el-tag class="tag" type="info">{{title}}</el-tag></div>
        <div style="clear:both"></div>
    </div>
</template>

<script>
export default {
    name: 'crb',
    props: {
        label_list:{
            type:Array,
            default:function(){
                return ['标签','标签','标签'];
            }
        }
    },
    data () {
        return {
        }
    }
}
</script>

<style scoped>
@import "../assets/side_block.css";
@import "../assets/tag.css";

.label_block_block{
    height:auto;
}

.el-divider{
    margin:0;
    margin-bottom:6px;
}
</style>




