<template>
    <div class="comment_block">
        <div class="comment_input">
            <el-input
                type="textarea"
                :autosize="{ minRows: 2, maxRows: 5}"
                :placeholder="replying?reply_placeholder:default_placeholder"
                v-model="comment"
                resize="none">
            </el-input>
            <el-button type="primary" :disabled="comment==''">发表</el-button>
            <el-button v-show="replying">取消回复</el-button>
            <div class="clear_both"></div>
        </div>
        <div class="others_comment">
            <ccb></ccb>
            <el-divider></el-divider>
            <ccb></ccb>
            <el-divider></el-divider>
            <ccb></ccb>
            <el-divider></el-divider>
        </div>
    </div>
</template>

<script>
export default {
    name:'comment',
    props: {
        pid:{
            type:Number,
            default: 0
        },
    },
    data () {
        return {
            comment:'',
            replying:false,
            default_placeholder:'请在此输入评论',
            reply_placeholder:'回复'
        }
    }
}
</script>

<style scoped>
.comment_block{
    padding:10px 0px;
}

h1{
    margin-bottom:10px
}

.comment_input{
    padding:0 10px;
}

.comment_input>>>textarea{
    border-radius: 3px;
    transition:0.1s all ease;
}

.comment_input>>>textarea::-webkit-scrollbar {
	width:6px;
	height:6px;
	padding-right:5px;
}

.comment_input>>>textarea::-webkit-scrollbar-thumb{
    border-radius: 3px;
	background-color: #ccc;
}

.comment_input>>>textarea::-webkit-scrollbar-corner{
    display: none;
}

.comment_input>>>.el-button{
    line-height: 18px;
    padding:5px 15px;
    border-radius: 3px;
    float: right;
    margin-top:10px;
    margin-left:10px;
}

.el-divider{
    margin:0;
}
</style>



