<template>
    <div class="data_block side_block">
        <div v-show="type=='default'">
            <div class="data_num">{{view}}</div>
            <h4>昨日阅读量</h4>
        </div>
        <div v-show="type=='default'">
            <div class="data_num">{{point}}</div>
            <h4>积分</h4>
        </div>
        <div v-show="type=='default'">
            <div class="data_num">{{collect}}</div>
            <h4>收藏</h4>
        </div>
        <div v-show="type=='default'">
            <div class="data_num">{{like}}</div>
            <h4>点赞</h4>
        </div>
        <div v-show="type=='only_fans'">
            <div class="data_num">{{fans}}</div>
            <h4>粉丝数</h4>
        </div>
        <div v-show="type=='only_fans'">
            <div class="data_num">{{subscribe}}</div>
            <h4>关注数</h4>
        </div>
        <div style="clear:both;float:none"></div>
    </div>
</template>

<script>
export default {
    name: 'crb',
    props: {
        view: {
            type: Number,
            default: 0
        },
        point: {
            type: Number,
            default: 0
        },
        collect: {
            type: Number,
            default: 0
        },
        like: {
            type: Number,
            default: 0
        },
        subscribe:{
            type: Number,
            default: 0
        },
        fans:{
            type: Number,
            default: 0
        },
        type:{
            type:String,
            default:'default'
        }
    },
    data () {
        return {
        }
    }
}
</script>

<style scoped>
@import "../assets/side_block.css";

.data_block{
    padding-bottom:10px;
}

.data_block h4{
    margin:18px 0;
}

.data_block>div{
    width:50%;
    text-align: center;
    float: left;
    cursor: pointer;
}

.data_block .data_num{
    font-size: 35px;
    font-weight: bold;
}
</style>




